Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/mclv/',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: function () {
        return import(/* webpackChunkName: "about" */ './views/About.vue')
      }
    },
    {
      path: '/subscribe',
      name: 'subscribe',
      component: function () {
        return import(/* webpackChunkName: "about" */ './views/Subscribe.vue')
      }
    },
    {
      path: '/more',
      name: 'more',
      component: function () {
        return import(/* webpackChunkName: "about" */ './views/More.vue')
      }
    },
    {
      path: '/dash',
      name: 'Dashboard',
      component: function () {
        return import(/* webpackChunkName: "about" */ './views/Dashboard.vue')
      }
    }
  ]
})
